import classes from "./Info.module.css";

function Info(props) {
	return (


		<div className={classes.info1}>
				<h1>{props.text}</h1>
				<div className={classes.im}>
					<img src={props.img} alt=""></img>
				<div><p>{props.info1}</p></div>
				</div>

			</div>


	);
}

export default Info;
